export default {
    /**--------------------------math-start----------------------**/
    /**
     * toPoint - 百分数转化为小数
     * @param percent
     */
    toPoint: function (percent) {
        var str = percent.replace('%', '');
        // @ts-ignore
        return str / 100;
    },
    /**
     * point - 小数转化为百分数
     * @param point
     */
    toPercent: function (point) {
        var str = Number(point * 100).toFixed(1);
        str += '%';
        return str;
    },
    /**
     * straightLineIntersection - 计算两个直线的交点
     * @param p1
     * @param p2
     * @param p3
     * @param p4
     */
    straightLineIntersection: function (p1, p2, p3, p4) {
        var x1 = p1.x, y1 = p1.y;
        var x2 = p2.x, y2 = p2.y;
        var x3 = p3.x, y3 = p3.y;
        var x4 = p4.x, y4 = p4.y;
        return {
            x: ((x3 - x4) * (x2 * y1 - x1 * y2) - (x1 - x2) * (x4 * y3 - x3 * y4)) /
                ((x3 - x4) * (y1 - y2) - (x1 - x2) * (y3 - y4)),
            y: ((y3 - y4) * (y2 * x1 - y1 * x2) - (y1 - y2) * (y4 * x3 - y3 * x4)) /
                ((y3 - y4) * (x1 - x2) - (y1 - y2) * (x3 - x4)),
        };
    },
    /**
     * getA3Piint - 计算两点p1,p2 距离p1点distance距离的点p3的坐标
     * @param {Point} - p1
     * @param {Point} - p2
     * @param {Number} - distance 与p1的距离
     * @return {{x: *, y: *}}
     */
    getA3Point: function (_a) {
        var p1 = _a.p1, p2 = _a.p2, distance = _a.distance;
        var Ax1 = p1.x, Ay1 = p1.y;
        var Ax2 = p2.x, Ay2 = p2.y;
        var dLA1A2 = Math.sqrt(Math.pow(Ax2 - Ax1, 2) + Math.pow(Ay2 - Ay1, 2)); // 计算A1A2的长度
        var Ax3 = (distance / dLA1A2) * (Ax2 - Ax1) + Ax1; // A3的横坐标
        var Ay3 = (distance / dLA1A2) * (Ay2 - Ay1) + Ay1; // A3的横坐标
        return {
            x: Ax3,
            y: Ay3,
        };
    },
    /**
     * getDistanceByBetweenPoint - 获取p1,p2两点间的距离
     * @param {Point} - p1
     * @param {Point} - p2
     * @return {number}
     */
    getDistanceByBetweenPoint: function (_a) {
        var p1 = _a.p1, p2 = _a.p2;
        var Ax1 = p1.x, Ay1 = p1.y;
        var Ax2 = p2.x, Ay2 = p2.y;
        return Math.sqrt(Math.pow(Ax2 - Ax1, 2) + Math.pow(Ay2 - Ay1, 2)); // 计算A1A2的长度
    },
    /**
     * clientToCtxPoint - 屏幕坐标转换成画布坐标
     * @param {Event} - event
     * @param {HTMLCanvasElement} - el
     * @return {x:number,y:number}
     */
    clientToCtxPoint: function (_a) {
        var event = _a.event, el = _a.el;
        var clientX = event.clientX, clientY = event.clientY;
        var rect = el.getBoundingClientRect();
        return {
            x: clientX - rect.left,
            y: clientY - rect.top,
        };
    },
};
